html, body {
	height: 100%;
	overflow-x: hidden;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100%;
}

.Canvas {
  flex: 1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header-buttons {
	padding: 0 5px;
	background: #9A8866;
	border: 2px solid #3D3629;
	/* box-sizing: border-box; */
}

.App-header-buttons > button {
	width: 40px;
	height: 40px;
	margin: 5px 2.5px;
	padding: 0;
	background: #C2B7A3;
	border: 1px solid #5C513D;
	box-sizing: border-box;
	font-size: large;
	font-weight: bolder;
	font-family: 'Bender';
	cursor: pointer;
}

.App-header-buttons > button:hover {
	background: #7B6C51;
}

.App-header-buttons > button:hover > img, .App-header-buttons > button:focus > img {
	background: #7B6C51;
	filter: invert(100%);
}

.App-link {
  color: #61dafb;
}

.App-header {
	background-color: #282c34;
	min-height: 50px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	color: #9a8866;
	padding: 0 1em;
}

.App-header-left {
  font-size: calc(10px + 2vmin);
	display: flex;
	flex-direction: row;
	align-items: center;
	min-height: 50px;
  justify-content: flex-start;
}

.App-header-github-logo {
  width: 32px;
  height: 32px;
  margin-left: 1em;
}

.App-header-github {
  font-size: 10px;
  text-decoration: none;
  color: white;
  margin-left: 1em;
}

.App-header-title {
  color: #9a8866;
  text-decoration: none;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}